body {
  background-color: #141414;
  margin: 0;
  font-family: Montserrat, sans-serif;
}

ul, ol {
  list-style: none;
}

.hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: Ninja Naruto;
  src: url("Ninja Naruto.44633dd6.ttf");
}

.header {
  justify-content: center;
  align-items: center;
  padding: 20px 85px;
  display: flex;
}

.header__title {
  color: #f97905;
  font-family: Ninja Naruto;
  font-size: 30px;
  text-decoration: none;
  transform: translateY(6px);
}

.header__nav-box {
  justify-content: space-between;
  gap: 50px;
  margin: auto;
  display: flex;
  transform: translateY(4px);
}

.header__nav-box .nav__button {
  color: #f97905;
  background-color: inherit;
  cursor: pointer;
  border: 0;
  padding: 7.5px 7.5px 0;
  font-family: Ninja Naruto;
  font-size: 30px;
}

.header__nav-box .nav__button:hover, .header__nav-box .nav__button:focus {
  outline: 2.5px solid #f97905;
}

.header__account-box {
  align-items: center;
  gap: 15px;
  display: flex;
}

.header__account-box .account__button {
  color: #f97905;
  background-color: inherit;
  cursor: pointer;
  border: 1px solid #f97905;
  padding: 10px;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.header__account-box .account__button:hover, .header__account-box .account__button:focus {
  color: inherit;
  background-color: #f97905;
}

.account__name {
  color: #f97905;
  margin: 0;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500;
}

.dropdown__main-button {
  background-color: inherit;
  color: #f97905;
  border: 0;
  outline: 1px solid #f97905;
}

.dropdown__main-button:hover, .dropdown__main-button:focus {
  color: inherit;
  background-color: #f97905;
}

.dropdown__menu {
  background-color: inherit;
  outline: 1px solid #f97905;
  padding: 0;
}

.dropdown__item {
  color: #f97905;
  text-align: right;
}

.dropdown__item:hover, .dropdown__item:focus {
  color: inherit;
  background-color: #f97905;
}

.hero {
  padding: 20px 85px;
}

.hero__title {
  color: #f97905;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  text-align: center;
  font-size: 30px;
  font-weight: 900;
}

.hero__text {
  color: #f97905;
  letter-spacing: 1.2px;
  text-indent: 20px;
  font-size: 20px;
  line-height: 25px;
}

.section__form {
  padding: 20px 85px;
}

.form {
  width: 1000px;
  border: 1px solid #f97905;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 25px;
  margin: auto;
  padding: 15px;
  display: flex;
}

.form__label {
  color: #f97905;
  letter-spacing: 1.2px;
  text-align: center;
  font-size: 20px;
  font-weight: 900;
}

.form__input {
  width: 500px;
  height: 36px;
  color: #fff;
  background-color: #ad5400;
  border: none;
  border-radius: 18px;
  padding: 5px 10px;
  font-size: 20px;
}

.form__input::-ms-input-placeholder {
  color: #141414;
}

.form__input::placeholder {
  color: #141414;
}

.form__input:not(:placeholder-shown) {
  background-color: #f97905;
}

.form__input:focus {
  background-color: #f97905;
  outline: 2px solid #fff;
}

.form__button {
  width: 120px;
  height: 30px;
  color: #141414;
  cursor: pointer;
  background-color: #f97905;
  border: none;
  border-radius: 4px;
  font-size: 20px;
}

.form__button:focus, .form__button:hover {
  color: #fff;
  outline: 2px solid #fff;
}

.modal-content {
  background-color: #f97905;
}

.modal-header {
  border: 0;
}

.label-sign_up {
  color: #000;
  margin-bottom: 5px;
  margin-left: 20px;
}

.input {
  width: 100%;
  color: #fff;
  background-color: #ad5400;
  border: none;
  border-radius: 18px;
  padding: 5px 10px;
  font-size: 20px;
}

.input:focus {
  background-color: #141414;
  outline: 2px solid #fff;
}

.input.js-email, .input.js-email__log {
  margin-bottom: 10px;
}

.button-sign_up {
  width: 120px;
  height: 30px;
  color: #f97905;
  cursor: pointer;
  background-color: #141414;
  border: none;
  border-radius: 4px;
  margin: 20px auto auto;
  font-size: 20px;
  display: block;
}

.button-sign_up:focus, .button-sign_up:hover {
  color: #fff;
  outline: 2px solid #fff;
}

.requests {
  padding: 60px 85px;
}

.requests-item {
  background-color: #f97905;
  border-radius: 18px;
  margin-bottom: 10px;
  padding: 5px 10px;
}

